.modal-card-container {
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.book-card {
  width: 90%;
  max-width: 300px;
  text-align: center;
  background: #fff;
  border-radius: 5px;
  margin: 20px;
  padding: 20px;
  position: relative;
  top: 30%;
  overflow: hidden;
  box-shadow: 0 2px 4px #0003;
}

.close-button {
  cursor: pointer;
  color: #555;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.close-button:hover {
  color: #000;
}

.add-to-cart-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 20px;
}

.add-to-cart-button:hover {
  background-color: #0056b3;
}

.modalDiv {
  position: fixed;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

